<template>
    <v-scroll-view>
        <v-cell>
            <v-switch v-model="isAccordion">accordion</v-switch>
        </v-cell>
        <v-collapse :is-accordion="isAccordion">
            <v-collapse-item :is-open="true">
                <template slot="header">
                    props: is-accordion
                </template>
                is-accordion="ture", 
            </v-collapse-item>
            <v-collapse-item title="props: title" :is-open="false">
                collapse-itemtitle/is-open
            </v-collapse-item>
            <v-collapse-item title="slot: header" :is-open="false">
                , collapse-itemname="header"slot
            </v-collapse-item>
        </v-collapse>
    </v-scroll-view>
</template>
<script>
import VCell from '@/packages/Cell/Cell';
import VSwitch from '@/packages/Switch/Switch';
import VCollapse from '@/packages/Collapse/Collapse';
import VCollapseItem from '@/packages/Collapse/CollapseItem';
import VScrollView from '@/packages/ScrollView/ScrollView';

export default {
    name: 'CollapseDemo',

    data() {
        return {
            isAccordion: true
        };
    },

    components: {
        VCell,
        VSwitch,
        VCollapseItem,
        VCollapse,
        VScrollView
    }
};
</script>
<style scope lang="scss">
@import '../scss/variables.scss';

</style>


