<template>
    <div @touchstart="touchstart" @touchmove="touchmove" @touchend="touchend" class="atom-virtual-scroller">
        <div ref="body" :style="style" @transitionend="transitionend" @webkitTransitionend="transitionend" :class="bodyClass" class="atom-virtual-scroller__body">
            <slot></slot>
        </div>
    </div>
</template>
<script>
import { getHeight, getWidth, getTime } from '../../utils/dom';
import momentum from './momentum';
import debounce from 'lodash/debounce';
export default {
    name: 'AtomVirtualScroller',

    props: {
        // isCalcByTouch: {
        //     type: Boolean,
        //     default: false
        // },
        preventDefaultException: {
            type: Object,
            default: () => ({ tagName: /^(INPUT|TEXTAREA|BUTTON|SELECT)$/ })
        },

        isBounce: {
            type: Boolean,
            default: true
        },

        bounceTime: {
            type: Number,
            default: 600
        },

        speed: {
            type: [String, Number],
            default: 300
        },

        isDisabled: {
            type: Boolean,
            default: false
        },

        sensitivity: {
            type: Number,
            default: 10
        },

        threshold: {
            // , 
            type: Number,
            default: 100
        },

        edgeThreshold: {
            type: Number,
            default: 150
        },

        isPreventDefault: {
            type: Boolean,
            default: true
        },

        isStopPropagation: {
            type: Boolean,
            default: false
        },

        value: {
            type: Object,
            default: () => ({ scrollLeft: 0, scrollTop: 0 }) // , 
        },

        isLockX: {
            type: Boolean,
            default: true
        },

        isLockY: {
            type: Boolean,
            default: false
        },

        moveRatio: {
            type: Number,
            default: 0.3
        },

        directionLockThreshold: {
            type: Number,
            default: 5
        },

        hasBufferMove: {
            type: Boolean,
            default: true
        },

        bufferThreshold: {
            // 
            type: Number,
            default: 15
        },

        hasReset: {
            type: Boolean,
            default: true
        },

        bodyStyle: {
            type: Object,
            default: () => {}
        },

        bodyClass: {
            type: Object,
            default: () => {}
        },

        maxHolderTime: {
            type: Number,
            default: 300
        },

        isBindBody: {
            type: Boolean,
            default: false
        },

        bodyClass: {
            type: [Object, Array],
            default: () => {}
        }
    },

    data() {
        return {
            isInTransition: false,
            transitionTimingFunction: 'cubic-bezier(0.1, 0.57, 0.1, 1)',
            isMoved: false,
            isDragging: false,
            isAnimating: false,
            isResting: false, // 
            startTime: 0,
            endTime: 0,
            transitionDuration: 0,
            warpWidth: 0,
            warpHeight: 0,
            startPointY: 0, // 
            startPointX: 0,
            x: 0,
            y: 0,
            maxTranslateX: 0,
            maxTranslateY: 0,
            startX: 0,
            startY: 0,
            deltaX: 0,
            deltaY: 0,
            direction: undefined,
            // new
            distX: 0, // touchstarttouchend
            distY: 0,
            isInTransition: false
        };
    },

    created() {
        // 
        this.x = -this.value.x;
        this.y = -this.value.y;
    },

    mounted() {
        this.updateSize();
        // window.addEventListener('resize', this.updateSize);
    },

    methods: {
        /**
         * /, 
         * , updateSize, 
         * resize
         * 
         * webpack
         */
        updateSize() {
            if (this.isLockY) {
                this.warpWidth = getWidth(this.$el);
            } else if (this.isLockX) {
                this.warpHeight = getHeight(this.$el);
            }
        },

        /**
         * bodyx
         */
        getPosition() {
            // https://github.com/nolimits4web/Swiper/blob/master/src/utils/utils.js
            // , swiper.jsgetPosition
            const style = getComputedStyle(this.$refs.body, null);
            const matrix = style.transform.split(',');
            return { x: Math.round(parseFloat(matrix[4] || matrix[12])), y: Math.round(parseFloat(matrix[5] || matrix[13])) };
        },
        /**
     * iscrollx,ytranslatex/y
     */
        touchstart(e) {
            // touch
            if (this.isDisabled) return;
            // scroll, 
            this.updateSize();
            // isStopPropagation | preventDefault, return false 
            this.isStopPropagation && e.stopPropagation();
            // 
            this.isPreventDefault && e.preventDefault();
            // ==========  ==========
            const point = e.touches ? e.touches[0] : e;

            // 0
            this.transitionDuration = 0;

            // 
            this.startTime = getTime();

            let { x, y } = this.getPosition();
            this.startX = x;
            this.startY = y;
            this.x = this.startX;
            this.y = this.startY;

            // touchmove
            this.distX = 0;
            this.distY = 0;

            this.isAnimating = false;

            // 
            this.startPointY = point.pageY;
            this.startPointX = point.pageX;

            // translate
            this.startX = this.x;
            this.startY = this.y;

            // , scroll-start
            this.isMoved = false;

            // 
            this.direction = undefined;

            // 
            this.$emit('before-scroll-start', this.position);
        },

        touchmove(e) {
            // touch[]
            if (this.isDisabled) return;

            // x/ylock[]
            if (this.isLockX && this.isLockY) return;

            this.isStopPropagation && e.stopPropagation();
            this.isPreventDefault && e.preventDefault();

            // 
            const now = getTime();

            // 
            const point = e.touches ? e.touches[0] : e;

            // touchmove
            let deltaX = point.pageX - this.startPointX;
            let deltaY = point.pageY - this.startPointY;

            // ,
            this.startPointY = point.pageY;
            this.startPointX = point.pageX;

            // 
            this.distX += deltaX;
            this.distY += deltaY;

            const absDistX = Math.abs(this.distX);
            const absDistY = Math.abs(this.distY);

            // xy10px(), 
            if (now - this.endTime > 300 && this.sensitivity > absDistX && this.sensitivity > absDistY) return;

            // 
            this.transitionTimingFunction = 'cubic-bezier(0.1, 0.57, 0.1, 1)';

            // touchmove, , touchstart
            if (undefined == this.direction && !(!this.isLockX && !this.isLockY)) {
                if (absDistX > absDistY + this.directionLockThreshold) {
                    this.direction = 'x';
                } else if (absDistY > absDistX + this.directionLockThreshold) {
                    this.direction = 'y';
                } else {
                    this.direction = 'n';
                }
            }

            // , 
            if ('x' === this.direction) {
                deltaY = 0;
            } else if ('y' === this.direction) {
                deltaX = 0;
            }

            if (!this.isLockX) {
                this.x += deltaX * (0 < this.x || this.minX > this.x ? this.moveRatio : 1);
            }

            if (!this.isLockY) {
                this.y += deltaY * (0 < this.y || this.minY > this.y ? this.moveRatio : 1);
            }

            // pull-down/pull-up
            if (0 < this.y) {
                this.$emit('pull-down', this.position);
            } else if (this.minY > this.y) {
                this.$emit('pull-up', this.position);
            }

            // pull-right/pull-left
            if (0 < this.x) {
                this.$emit('pull-right', this.position);
            } else if (this.minX > this.X) {
                this.$emit('pull-left', this.position);
            }

            // , 
            if (this.maxHolderTime < now - this.startTime) {
                this.startTime = now;
                this.startPointY = point.pageY;
                this.startPointX = point.pageX;
                this.startY = this.y;
                this.startX = this.x;
            }

            if (!this.isMoved) {
                this.$emit('scroll-start', this.position);
            }

            this.isMoved = true;

            // 
            this.$emit('scroll', this.position);
        },

        touchend(e) {
            // touch
            if (this.isDisabled) return;

            this.isStopPropagation && e.stopPropagation();
            this.isPreventDefault && e.preventDefault();

            // touchmove10px, false === isMoved
            if (!this.isMoved) {
                this.$emit('scroll-cancel');
            }

            const point = e.changedTouches ? e.changedTouches[0] : e;
            const distanceX = this.x - this.startX;
            const distanceY = this.y - this.startY;

            // 
            // 
            if (this.resetPosition(this.bounceTime)) {
                return;
            }

            // 
            this.endTime = getTime();
            const timeDiff = this.endTime - this.startTime;

            // 200ms
            if (200 > timeDiff) {
                this.isAnimating = true;

                if (!this.isLockX) {
                    const { destination, duration } = momentum(this.x, this.startX, timeDiff, this.minX, this.isBounce ? this.warpWidth : 0, 0.0006);
                    this.scrollTo(destination, 0, duration);
                    if (0 === duration) {
                        this.$emit('scroll-end', this.position);
                    }
                }

                if (!this.isLockY) {
                    const { destination, duration } = momentum(this.y, this.startY, timeDiff, this.minY, this.isBounce ? this.warpHeight : 0, 0.0006);
                    this.scrollTo(0, destination, duration);
                    if (0 === duration) {
                        this.$emit('scroll-end', this.position);
                    }
                }
            } else {
                // , 
                this.$emit('input', this.position);
                this.$emit('scroll-end', this.position);
            }
            this.$emit('touchend', this.position);
        },

        transitionend() {
            this.isAnimating = false;
            // 
            if (!this.resetPosition(this.bounceTime)) {
                this.isInTransition = false;
            }
            this.$emit('input', this.position);
            // this.$emit('scroll-end', this.position);
            this.$emit('transition-end', {...this.position, type: this.isResting ? 'reset': 'inertia'});
            if(this.isResting) this.isResting = false;
        },

        /**
         * [!!!!!!, 2transiton-end]
         * 
         * @augments {Number} time
         * @returns {Boolean} 
         * */
        resetPosition(time = 0) {
            let x = this.x;
            let y = this.y;

            if ('x' === this.direction) {
                if (0 < this.x) {
                    x = 0;
                } else if (this.minX > this.x) {
                    x = this.minX;
                }
            } else if ('y' === this.direction) {
                if (0 < this.y) {
                    y = 0;
                } else if (this.minY > this.y) {
                    y = this.minY;
                }
            }

            // 
            if (x === this.x && y === this.y) {
                return false;
            }

            //
            this.scrollTo(x, y, time, 'cubic-bezier(0.25, 0.46, 0.45, 0.94)');
            this.isResting = true;
            this.$emit('rest-position');
            return true;
        },

        /**
         * 
         */
        scrollTo(x, y, duration, easing = 'cubic-bezier(0.1, 0.57, 0.1, 1)') {
            this.x = x;
            this.y = y;
            this.transitionDuration = duration;
            this.transitionTimingFunction = easing;
            this.isInTransition = 0 < duration;
        }
    },

    computed: {
        style() {
            return [
                {
                    transform: `translate3d(${Math.round(this.x)}px, ${Math.round(this.y)}px, 0)`,
                    transitionDuration: `${this.transitionDuration}ms`,
                    transitionTimingFunction: this.transitionTimingFunction
                },
                this.bodyStyle
            ];
        },

        /**
         * minY
         */
        minY() {
            return this.warpHeight - getHeight(this.$refs.body, { isScroll: true });
        },

        maxY() {
            return 0;
        },

        /**
         * minX
         */
        minX() {
            return this.warpWidth - getWidth(this.$refs.body, { isScroll: true });
        },

        maxX() {
            return 0;
        },

        /**
         * 
         */
        position() {
            return {
                x: -Math.round(this.x),
                y: -Math.round(this.y)
            };
        }
    },

    watch: {
        value: {
            deep: true,
            handler(value) {
                this.transitionDuration = this.speed;
                this.x = -value.x;
                this.y = -value.y;
            }
        }
    }
};
</script>
<style scoped lang=scss>
.atom-virtual-scroller {
    position: relative;
    touch-action: pan-x;
    touch-action: pan-y;
    user-select: none;
    -webkit-user-drag: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: hidden;
    > .atom-scroller__body {
        position: relative;
        width: 100%;
        user-select: none;
        /* transition-timing-function: ease-out; */
        transition-timing-function: cubic-bezier(0.1, 0.57, 0.1, 1);
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
        .table {
            /* display: table,  */
            display: table;
        }
    }
}
</style>


