<template>
    <v-scroll-view class="demo-page">
        <v-group>
            <template slot="header">
                
                <span class="text-danger">arrow</span>, 
            </template>

            <v-cell :arrow="0">arrow = 0</v-cell>
            <v-cell :arrow="90">arrow = 90</v-cell>
            <v-cell :arrow="180">arrow = 180</v-cell>
            <v-cell :arrow="270">arrow = 270</v-cell>
        </v-group>

        <v-group>
            <template slot="header">
                <span class="text-danger">extra</span>/
            </template>
            <v-cell>
                <template slot="extra"></template>
            </v-cell>
            <v-cell>
                <template slot="extra"></template>
            </v-cell>
        </v-group>
        <v-group>
            <template slot="header">
                <span class="text-danger">switch/radio/checkbox</span>
            </template>

            <v-cell>
                <v-switch></v-switch>
            </v-cell>
            <v-cell>
                <v-checkbox></v-checkbox>
            </v-cell>

            <v-cell>
                <v-radio v-model="radioValue" value="1"></v-radio>
            </v-cell>

            <v-cell>
                <v-radio v-model="radioValue" value="2"></v-radio>
            </v-cell>


        </v-group>

    </v-scroll-view>
</template>
<script>
import VCell from '@/packages/Cell/Cell';
import VGroup from '@/packages/Group/Group';
import VRadio from '@/packages/Radio/Radio';
import VSwitch from '@/packages/Switch/Switch';
import VCheckbox from '@/packages/Checkbox/Checkbox';
import VScrollView from '@/packages/ScrollView/ScrollView';
export default {
    name: 'CellDemo',

    data() {
        return {
            radioValue: 1
        };
    },

    components: {
        VCell,
        VScrollView,
        VGroup,
        VSwitch,
        VRadio,
        VCheckbox
    }
};
</script>
<style scoped lang="scss">
@import '../scss/variables.scss';
.demo-page{height: calc(100% - 55px);background: $lightest;}
</style>



